import React, { Component } from 'react';
import { Icon } from 'antd';
import moment from 'moment';
import * as ApiProvider from '../../API/ApiProvider'
import VitalWarningValueFix from './VitalWarningValueFix'


import './Vitalsign.scss'

export default class VitalWarningValueRow extends Component {
  state = {
    fixData: false
  }

  editData = () => {
    this.setState({
      fixData: true
    })
  }

  cancelData = () => {
    this.setState({
      fixData: false
    })
  }

  delectData = () => {
    const parserTime = document.querySelector(`div[title="${this.props.index}"]`).querySelector('#DataDate').textContent
    const tempTime = moment(parserTime, "YYYY/MM/DD HH:mm").format("YYYYMMDDHHmmss")
    const body = {
      'id': this.props.id,
      'time': tempTime
    }

    ApiProvider.delectWarningValue(body)
      .then(data => {
        this.props.callBack()
      })
      .catch(err => { console.log(err) })
  }

  modifyData = () => {
    const { id, element } = this.props
    return (
      <VitalWarningValueFix element={element} fixStatus={true} id={id} review={this.props.callBack} cancelFix={this.cancelData}></VitalWarningValueFix>
    )
  }

  render() {
    const { index, element } = this.props
    const { fixData } = this.state

    const date = moment(element.centerwarningvaluekey.time, "YYYYMMDDHHmmss").format("YYYY/MM/DD HH:00")

    return (
      <div>
        {fixData ? this.modifyData() :
          < div title={index} key={index} style={{ display: "grid", gridTemplateColumns: "1fr 1fr 1fr 1fr 1fr 1fr 0.3fr", border: "1px solid rgba(232, 232, 232, 1)", borderTop: "0px", alignContent: "center", height: "35px" }}>
            <div id='DataDate' style={{ fontSize: "14px", paddingLeft: "10px" }}>{date}</div>
            <div style={{ fontSize: "14px", paddingLeft: "10px" }}>Hight: {element.hr_hight} / Low: {element.hr_low}</div>
            <div style={{ fontSize: "14px", paddingLeft: "10px" }}>Hight: {element.bp_hight} / Low: {element.bp_low}</div>
            <div style={{ fontSize: "14px", paddingLeft: "10px" }}>Hight: {element.rr_hight} / Low: {element.rr_low}</div>
            <div style={{ fontSize: "14px", paddingLeft: "10px" }}>Hight: {element.spo2_hight} / Low: {element.spo2_low}</div>
            <div style={{ fontSize: "14px", paddingLeft: "10px" }}>Hight: {element.bt_hight} / Low: {element.bt_low}</div>
            <div className="checkButton">
              <Icon type="edit" onClick={this.editData} />
              <Icon type="close" style={{ marginLeft: "5px" }} onClick={this.delectData} />
            </div>
          </div>}
      </div>
    );
  }
}
